@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,400i,700,900&subset=latin-ext);
@font-face {
  font-family: 'Roboto';
  src: local('Roboto'), url(/najstolar/static/media/Roboto-Regular.03523cf5.ttf) format('ttf');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body, #app, #app>div, #root {
  min-height: 100%
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



@media (max-width: 575px) {                  
    .hide-on-xs {
       display: none;
    }
 }

 @media (min-width: 576px) {
    .show-on-xs {
        display: none;
     }
 }

